<template>
  <div id="app-container" class="app-container">
    <h2
      class="text-xl font-medium m-0 p-0 mt-2 w-full border-0 border-b border-solid border-gray-400"
    >{{ title }}</h2>
    <h3 class="text-xs font-medium m-0 p-0 mt-4 uppercase">Your Savings:</h3>
    <h4 class="text-2xl font-bold m-0 p-0 mt-2">₹ {{totalIncome - totalExpenses}}</h4>
    <div class="flex bg-white p-5 mt-2 text-center shadow-lg">
      <div class="w-1/2 border-0 border-r-2 border-solid border-gray-400">
        <span class="text-green-700 font-medium text-xl">INCOME</span>
        <br />
        ₹ {{totalIncome}}
      </div>
      <div class="w-1/2">
        <span class="text-red-700 font-medium text-xl">EXPENSE</span>
        <br />
        ₹ {{totalExpenses}}
      </div>
    </div>
    <div class="bg-white mt-2 p-2 shadow-lg mb-2">
      <div class="inline-flex mt-2 w-full items-end">
        <div
          v-if="type === 'EXPENSE'"
          class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded cursor-pointer select-none"
          v-on:click="toggleType($event)"
        >{{type}}</div>
        <div
          v-if="type === 'INCOME'"
          class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded cursor-pointer select-none"
          v-on:click="toggleType($event)"
        >{{type}}</div>
        <p class="text-xs m-0 p-0 text-gray-600">
          (Click this button to change as
          <span class="lowercase">{{type}}</span>.)
        </p>
      </div>
      <form @submit="checkForm($event)">
        <div class="flex mt-2">
          <div class="w-1/2">
            <input
              class="appearance-none block text-gray-700 border border-gray-500 py-2 mb-2 pl-2 leading-tight focus:outline-none focus:bg-white input-text"
              id="grid-first-name"
              type="text"
              placeholder="Enter Text Here."
              required
              v-model="text"
            />
          </div>
          <div class="w-1/2">
            <input
              class="appearance-none block text-gray-700 border border-gray-500 py-2 pl-2 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 input-text"
              id="grid-last-name"
              type="number"
              placeholder="Amount"
              required
              v-model="amount"
            />
          </div>
        </div>
        <div class="w-8 text-center mb-2">
          <button
            type="submit"
            class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded cursor-pointer select-none"
          >ADD</button>
        </div>
      </form>
    </div>
    <h2
      class="text-base font-bold m-0 p-0 w-full border-0 border-b border-solid border-gray-400"
    >History - {{month}}, {{year}}</h2>
    <div>
      <div>
        <div class="flex w-full bg-green-600 text-white mt-1 py-2 font-bold">
          <div class="w-1/2 pl-2">Total Income</div>
          <div class="w-1/2 pr-2 text-right">₹ {{totalIncome}}</div>
        </div>
        <div>
          <div
            v-for="(item, index) in incomes"
            :key="index"
            class="flex w-full text-sm py-1 border-0 border-b border-solid border-gray-400 relative"
          >
            <div
              class="absolute right--10 bg-black p-1 rounded-r-lg cursor-pointer text-white font-bold select-none"
              v-on:click="removeIt($event, index, 'INCOME')"
            >X</div>
            <div class="w-1/2 pl-2">{{ item.text }}</div>
            <div class="w-1/2 pr-2 text-right">₹ {{ item.amount }}</div>
          </div>
        </div>
      </div>
      <div>
        <div class="flex w-full bg-red-600 text-white mt-1 py-2 font-bold">
          <div class="w-1/2 pl-2">Total Expense</div>
          <div class="w-1/2 pr-2 text-right">₹ {{totalExpenses}}</div>
        </div>
        <div>
          <div
            v-for="(item, index) in expenses"
            :key="index"
            class="flex w-full text-sm py-1 border-0 border-b border-solid border-gray-400 relative"
          >
            <div
              class="absolute right--10 bg-black p-1 rounded-r-lg cursor-pointer text-white font-bold select-none"
              v-on:click="removeIt($event, index, 'EXPENSE')"
            >X</div>
            <div class="w-1/2 pl-2">{{ item.text }}</div>
            <div class="w-1/2 pr-2 text-right">₹ {{ item.amount }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MainPage',
  data () {
    return {
      title: 'Expense Tracker',
      totalIncome: 0,
      totalExpenses: 0,
      month: [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December'
      ][new Date().getMonth()],
      year: new Date().getFullYear(),
      type: 'EXPENSE',
      text: null,
      amount: null,
      incomes: [],
      expenses: []
    }
  },
  methods: {
    toggleType: function () {
      this.type = this.type === 'EXPENSE' ? 'INCOME' : 'EXPENSE'
    },
    checkForm: function (e) {
      e.preventDefault()
      if (this.type === 'EXPENSE') {
        this.expenses.push({ text: this.text, amount: this.amount })
      } else {
        this.incomes.push({ text: this.text, amount: this.amount })
      }
      this.totalIncome = this.incomes.reduce((total, { amount }) => total + Math.round(amount), 0)
      this.totalExpenses = this.expenses.reduce((total, { amount }) => total + Math.round(amount), 0)
      this.text = null
      this.amount = null
    },
    removeIt: function (e, idx, type) {
      if (this.type === 'EXPENSE') {
        this.expenses.splice(idx, 1)
      } else {
        this.incomes.splice(idx, 1)
      }
      this.totalIncome = this.incomes.reduce((total, { amount }) => total + Math.round(amount), 0)
      this.totalExpenses = this.expenses.reduce((total, { amount }) => total + Math.round(amount), 0)
    }
  }
}
</script>

<style scoped>
.app-container {
  width: 350px;
}
.right--10 {
  right: -18px;
  top: 0px;
}
.input-text {
  width: 154px;
}
</style>



